.component--TopicHomePage .tip {
    margin: 0.5em;
    padding: 0.5em 0.8em;
    display: inline-block;
    background: #efefef;
    border-radius: 5px;
    vertical-align: top;
}

.component--TopicHomePage .tip code {
    display: block;
    background: #e5efff;
    border-radius: 2px;
    padding: 0.5em;
    margin: 0.3em 0;
}


