.component--PageLoader {
    position: fixed;
    top: 0;
    width: 100%;
    border-bottom: thin solid transparent;
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(white)) padding-box, repeating-linear-gradient(-90deg, #526492 0, #526492 70%, transparent 0, transparent 100%) 0 / 5mm 1mm;
    background: -webkit-linear-gradient(white, white) padding-box, -webkit-repeating-linear-gradient(right, #526492 0, #526492 70%, transparent 0, transparent 100%) 0 / 5mm 1mm;
    background: -o-linear-gradient(white, white) padding-box, -o-repeating-linear-gradient(right, #526492 0, #526492 70%, transparent 0, transparent 100%) 0 / 5mm 1mm;
    background: linear-gradient(white, white) padding-box, repeating-linear-gradient(-90deg, #526492 0, #526492 70%, transparent 0, transparent 100%) 0 / 5mm 1mm;
    -webkit-animation: ants 12s linear infinite;
            animation: ants 12s linear infinite;
    z-index: 1000;
    height: 1px;
    overflow: hidden;
}

.component--PageLoader > div {
    display: inline-block;
    width: 100%;
    font-size: 1em;
    height: 1px;
    text-align: center;
    background: #efefef;
}

@-webkit-keyframes ants {
    to {
        background-position: 100% 100%
    }
}

@keyframes ants {
    to {
        background-position: 100% 100%
    }
}


