.component--LayoutFooter {
    font-size: 0.8em;
    position: fixed;
    bottom: 0;
    border-top: 3px double #efefef;
    width: 100%;
    padding: 0;
    margin: 0;
    background: #ffffff;
}

.component--LayoutFooter > div {
    padding: 0.3em 0.5em;
}
.component--LayoutFooter > div > div {
    display: inline-block;
}

.component--LayoutFooter .message {
}

.component--LayoutFooter .home {
    float: right;
}

.component--LayoutFooter .help {
    float: right;
    margin-right: 0.5em;
}


